<template>
  <div class="login">
    <h1 align-h="center">This is Login Page!</h1>

    <b-container>
      <b-row align-h="center" class="login1">
        <b-col cols='5'>
          <b-card class="login2">
            <h3 class="login3"></h3>
              <b-form @submit="login" 
                @reset="onReset" v-if="show">

                <b-form-group id="ID-InputGroup"
                              label="ID:"
                              label-for="id-input">
                  <b-form-input id="id-input"
                                type="text"
                                v-model="form.name"
                                required
                                placeholder=" ">
                  </b-form-input>
                </b-form-group>
                
                <b-form-group id="password-InputGroup"
                              label="Password:"
                              label-for="pwd-input">
                  <b-form-input id="pwd-input"
                                type="password"
                                v-model="form.password"
                                required
                                placeholder="">
                  </b-form-input>
                </b-form-group>
                
                <b-form-group id="RememberCheck">
                  <b-form-checkbox-group v-model="form.checked" id="Checks">
                    <b-form-checkbox value="me"> </b-form-checkbox>
                  </b-form-checkbox-group>
                </b-form-group>

                <div class="d-flex justify-content-between">
                  <div>
                    <b-button type="submit" variant="primary"></b-button>&nbsp;
                    <b-button type="reset" variant="danger"></b-button>
                  </div>
                  <div>
                    <a href="#" v-b-modal.modal1> </a>
                  </div>
                </div>
              
              </b-form>
          </b-card>
        </b-col>
      </b-row>
    </b-container>

    <b-modal id="modal1" title="Forgot Password">
      Nothing yet.
    </b-modal>
  
  </div>
</template>

<script>
export default {
  name: 'Login',
  data () {
    return {
      form: {
        name: '',
        password: '',
        checked: []
      },
      show: true
    }
  },
  methods: { 
    login () {
      let self = this
      this.$http.post('/login', {
        username: this.form.name, 
        password: this.form.password
      })
      .then((res) => { 
        const status = res.status
        console.log(res)
        //redirect logic
        if (status == '200') {
          onReset()
          self.$router.push('/main');
        }
      })
      .catch((err) => {
        console.log(err)
      })
    },
    onReset (evt) {
      evt.preventDefault()
      /* Reset our form values */
      this.form.email = ''
      this.form.password = ''
      this.form.checked = []
      /* Trick to reset/clear native browser form validation state */
      this.show = false
      this.$nextTick(() => { this.show = true })
    },
    signup() {

    }

  }
}
</script>



